            <img src="../assets/content-team.svg" alt="" v-if="svgCard == 'content-team'">
            <img src="../assets/road-to-knowledge.svg" alt="" v-if="svgCard == 'road-to-knowledge'">
            <h3>{{ theme }}</h3>
        </div>
        <div class="description">
            <h3>{{ titleCard }}</h3>
