        </div>
    </div>
  </div>
</template>

<script>



export default {
  name: 'projectCard',
  props : {
    titleCard: String,
    svgCard: String,
    descriptionCard: String,
    theme: String,
    link: String,
  },
}
</script>


<style scoped lang="scss">

@import '../assets/_variables.scss';
@import '../assets/_mixins.scss';

.card {
