<template>
  <div class="skills-progress">
    <p>{{ name }}</p>
    <div class="progress-bar" :style="cssProps">
        {{ hidden}}
    </div>
  </div>
</template>

<script>
export default {
    name : 'skillsProgress',
    props : {
        name: String,
        progressBar: String,
        hidden: String,
    },
    computed: {
        cssProps() {
            return {
                '--progressBar': this.progressBar + "%",
            }
        }
    }
}
</script>

<style lang="scss">

@import '../assets/_variables.scss';
@import '../assets/_mixins.scss';
@import '../assets/_animation.scss';

@keyframes progress-up {
    to {
        width : var(--progressBar);
    }
}

@keyframes progress-down {
    from {
        width : var(--progressBar);
    }
    to {
        width : 0;
    }
}

.skills-progress {
    margin-bottom : 3rem;
    display: flex;
    align-items: center;
    @include tablet {
        margin-bottom : 2rem;
    }
    p {
        width : 5rem;
        @include tablet {
            width : 3.5rem;
        }
    }
    .progress-bar {
        position: relative;
        margin-left: 2rem;
        width : 30rem;
        height : 1.3rem;
        border-radius: 50px;
        box-shadow:  20px 20px 41px #c4b7aa,
                    -20px -20px 41px #fff7e6;
        padding-left : 1rem;
        overflow: hidden;
        @include tablet {
            width : 34vw;
            height : 1rem;
        }
        &:hover {
            &::after {
                animation : progress-down 0.6s ease-out;
            }
        }
        &::after {
            content: '';
            z-index: 2;
            position: absolute;
            width : 0;
            height : 100%;
            border-radius: 50px;
            background: $beige-one;
            left : 0;
            animation : progress-up 1s ease-out forwards;
        }
    }
}

</style>